@use 'base/variables' as v;

.formulario{
    //background-color: v.$color_principal;
    padding: 3rem;
    //font-family: v.$font-family;
    padding-top: 4rem;
    margin: 4rem auto;
    max-width: 900px;
    border-radius: 8px; 
    background-color: v.$blanco;
    box-shadow: 0 0 100px rgba(255, 255, 255, 40);
}

.formulario h2{
    margin-bottom: 5rem;
    text-align: center;
    font-size: v.$h2_font-size;
    font-family: v.$font-family;
}
.formulario_campo{
    margin-bottom: 2rem;
    font-size: 1.3rem;
    font-weight: v.$weight_600;
    
}
.formulario_campo label{
    display: block;
    margin-bottom: 1rem;
    font-family: v.$font-family;
}
.formulario_campo input, .formulario_campo textarea{
    width: 100%;
    height: 4rem;
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.formulario_campo textarea{
    height: 200px;
    resize: none;
}
button[type="submit"]{
    width: 20%;
    height: 50px;
    background-color: #4CAF50;
    color: v.$blanco;
    padding: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: v.$large_font-size;
    font-weight: v.$weight_600;
    font-family: v.$font-family; 
}
button[type="submit"]:hover{
    background-color: v.$color_principal-dark;
    transition: .5s;
}
