@use 'base/variables' as v;
@use 'base/mixins' as m;


.tecnologias{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: v.$blanco;
    min-height: 100vh;
    max-width: 900px;
    width: 95%;
    overflow: hidden;
    padding: 5rem 5rem;
    margin: 2rem auto;
    border-radius: 8px;
    box-shadow: 0 0 100px rgba(255, 255, 255, 40);
}

.content{
    @include m.telefono{
        gap: 3rem;
        background: linear-gradient(180deg, rgba(2, 2, 2, 0.3) 0%, rgba(255,255,255,0) 100%);
        border-radius: 20px;
        width: 600px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    @include m.desktop{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        background: linear-gradient(180deg, rgba(2, 2, 2, 0.3) 0%, rgba(255,255,255,0) 100%);
        backdrop-filter: blur(30px);
        border-radius: 20px;
        width: min(900px, 100%);
        box-shadow: 0 0.5px 0 1px rgba(255,255,255, 0.2) inset, 0 1px 0 0 rgba(255,255,255, 0.65) inset, 04px 16px rgba(0,0,0, 0.1);
        z-index: 10; 
    } 
}
.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 350px;
    padding: 0 35px;
    text-align:  justify;
    padding-top: 2rem;

    p{
        color: v.$container_color;
        font-weight: 500;
        font-size: 1rem;
        margin-bottom: 20px;
        line-height: 1.5;
    }
}

.swiper{
    width:180px;
    height: 450px;
    padding: 50px 0;

    @include m.desktop{
    width: 250px;
    height: 450px;
    padding: 50px 0;  
    }
   
}
.swiper-slide{
    position: relative;
    box-shadow: 0 15px 50px rgba(0,0,0,0.2);
    border-radius: 10px;
    width: 100px;
}
.swiper-slide h2{
    position: relative;
    bottom: 0;
    left: 0;
    padding-top: 40px;
    text-align: center;
    color: v.$blanco;
    font-weight: 400;
    font-size: 1.1rem;
    line-height: 1.4;
    //margin: 0 0 20px 20px;
}
.swiper-slide:nth-child(1n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/iconos/front/front.avif) no-repeat 50% 50% / cover;
}
.swiper-slide:nth-child(2n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/iconos/back/back.avif) no-repeat 50% 50% / cover;
}
.swiper-slide:nth-child(3n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/iconos/herramientas/herramientas.avif) no-repeat 50% 50% / cover;
}
.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background-color: #ff3cac;
    background-image: linear-gradient(225deg, #ff3cac 0%, #784ba0 50%, #2b86c5 100%);
    animation: animate 25s linear infinite;
    bottom: -150px;
}
.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0;
    
}
.circles li:nth-child(2){
    
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0;
}
.circles li:nth-child(3){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}
@keyframes animate{
    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100%{
        transform: translateY(-100px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
    
}
